import { Skeleton } from "@rp/react/web/mui/core"

const LoadingFilter = ({ lines = 5 }) => (
  <div data-testid="panel-loader">
    <Skeleton variant="text" width="100%" height="52px" />

    {[...new Array(lines).keys()].map(key => (
      <Skeleton variant="text" width="100%" key={key} height="32px" />
    ))}
  </div>
)

export { LoadingFilter }
